/*
 * Application global variables.
 */

// Angular Material custom theme
// See https://material.angular.io/guide/theming for more details.
//
// You can also read https://medium.com/@tomastrajan/the-complete-guide-to-angular-material-themes-4d165a9d24d1
// for more insight about Angular Material theming.

@import "~@angular/material/theming";

// Define the palettes for your theme using the Material Design palettes available in palette.scss
// (imported above). For each palette, you can optionally specify a default, lighter, and darker
// hue.
$app-primary: mat-palette($mat-blue);
// stylelint-disable-next-line value-keyword-case
$app-accent: mat-palette($mat-pink, A200, A100, A400);

// The warn palette is optional (defaults to red).
$app-warn: mat-palette($mat-red);

// Create the theme object (a Sass map containing all of the palettes).
$app-theme: mat-light-theme($app-primary, $app-accent, $app-warn);

// Material design icons font path
$material-design-icons-font-directory-path: "~material-design-icons-iconfont/dist/fonts/";

// custom
$appColor: #12b188;
$backgroundColor: #fff;
$textColor: #fff;
$statusBarColor: #f0f0f0;
$buttonColor: #ffda31;
$buttonTextColor: #000;
$shadowColor: #666;
$notificationBackgroundColor: #B4E4D8;
$notificationTextColor: #000;
$notificationShadowColor: #BBB;
$bottomMenuHeight: 110px;
$bottomMenuTextColor: #888;
$bottomMenuShadowColor: #eee;
